
"use client";

import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogFooter,
  DialogClose,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import React from "react";
import type { User, UserRole } from "@/lib/types";
import { useToast } from "@/hooks/use-toast";
import { Eye, EyeOff } from "lucide-react";
import { useFirebase } from "@/firebase";
import { collection, doc, addDoc, updateDoc } from "firebase/firestore";
import { errorEmitter } from "@/firebase/error-emitter";
import { FirestorePermissionError } from "@/firebase/errors";

const userSchema = z.object({
  name: z.string().min(2, "Name must be at least 2 characters."),
  username: z.string().min(3, "Username must be at least 3 characters."),
  password: z.string().min(6, "Password must be at least 6 characters.").optional().or(z.literal('')),
  role: z.enum(['admin', 'varsity', 'novice'], { required_error: "A role is required." }),
});

interface CreateUserDialogProps {
    children: React.ReactNode;
    userToEdit?: User;
}

export default function CreateUserDialog({ children, userToEdit }: CreateUserDialogProps) {
  const [open, setOpen] = React.useState(false);
  const [showPassword, setShowPassword] = React.useState(false);
  const { toast } = useToast();
  const { firestore } = useFirebase();

  const form = useForm<z.infer<typeof userSchema>>({
    resolver: zodResolver(userSchema),
    defaultValues: userToEdit ? {
        name: userToEdit.name,
        username: userToEdit.username,
        role: userToEdit.role,
        password: "",
    } : {
      name: "",
      username: "",
      password: "",
      role: "novice",
    },
  });
  
  React.useEffect(() => {
    if (open) {
        form.reset(userToEdit ? {
            name: userToEdit.name,
            username: userToEdit.username,
            role: userToEdit.role,
            password: "",
        } : {
          name: "",
          username: "",
          password: "",
          role: "novice",
        });
    }
  }, [open, form, userToEdit]);

  async function onSubmit(values: z.infer<typeof userSchema>) {
    if (!firestore) return;

    if (userToEdit) {
        const updatedUserData = {
            name: values.name,
            username: values.username,
            role: values.role as UserRole,
        };
        const userDocRef = doc(firestore, 'users', userToEdit.id);
        updateDoc(userDocRef, updatedUserData).catch(error => {
             errorEmitter.emit('permission-error', new FirestorePermissionError({
                path: userDocRef.path,
                operation: 'update',
                requestResourceData: updatedUserData,
            }));
        });
        toast({ title: "User Updated", description: `User ${values.name} has been updated.` });
    } else {
        if (!values.password || values.password.length < 6) {
            form.setError("password", { message: "Password must be at least 6 characters." });
            return;
        }
        const newUser: Omit<User, 'id'> = {
            name: values.name,
            username: values.username,
            avatarUrl: '', 
            role: values.role as UserRole
        };
        const usersCollection = collection(firestore, 'users');
        addDoc(usersCollection, newUser).catch(error => {
            errorEmitter.emit('permission-error', new FirestorePermissionError({
                path: 'users',
                operation: 'create',
                requestResourceData: newUser
            }));
        });
        toast({ title: "User Created", description: `User ${newUser.name} has been created.` });
    }
    setOpen(false);
  }

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>{children}</DialogTrigger>
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle className="font-headline">{userToEdit ? 'Edit User' : 'Create New User'}</DialogTitle>
          <DialogDescription>
            {userToEdit ? 'Update the details for this user.' : 'Fill in the details to create a new user account.'}
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4 py-4">
            <FormField control={form.control} name="name" render={({ field }) => (
                <FormItem>
                  <FormLabel>Full Name</FormLabel>
                  <FormControl><Input {...field} placeholder="e.g. John Doe" /></FormControl>
                  <FormMessage />
                </FormItem>
            )} />
             <FormField control={form.control} name="username" render={({ field }) => (
                <FormItem>
                  <FormLabel>Username</FormLabel>
                  <FormControl><Input {...field} placeholder="e.g. jdoe" /></FormControl>
                  <FormMessage />
                </FormItem>
            )} />
             <FormField
                control={form.control}
                name="password"
                render={({ field }) => (
                    <FormItem>
                    <FormLabel>Password</FormLabel>
                    <FormControl>
                        <div className="relative">
                        <Input type={showPassword ? "text" : "password"} placeholder={userToEdit ? "New password (optional)" : "••••••••"} {...field} />
                        <Button
                            type="button"
                            variant="ghost"
                            size="icon"
                            className="absolute right-1 top-1/2 h-7 w-7 -translate-y-1/2 text-muted-foreground"
                            onClick={() => setShowPassword(!showPassword)}
                            aria-label={showPassword ? "Hide password" : "Show password"}
                        >
                            {showPassword ? <EyeOff size={16} /> : <Eye size={16} />}
                        </Button>
                        </div>
                    </FormControl>
                    <FormMessage />
                    </FormItem>
                )}
            />
            <FormField control={form.control} name="role" render={({ field }) => (
                <FormItem>
                  <FormLabel>Role</FormLabel>
                  <Select onValueChange={field.onChange} value={field.value}>
                      <FormControl><SelectTrigger><SelectValue placeholder="Select a role" /></SelectTrigger></FormControl>
                      <SelectContent>
                          <SelectItem value="novice">Novice</SelectItem>
                          <SelectItem value="varsity">Varsity</SelectItem>
                          <SelectItem value="admin">Admin</SelectItem>
                      </SelectContent>
                  </Select>
                  <FormMessage />
                </FormItem>
            )} />
             <DialogFooter className="pt-4">
                <DialogClose asChild><Button type="button" variant="outline">Cancel</Button></DialogClose>
                <Button type="submit">{userToEdit ? 'Save Changes' : 'Create User'}</Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}
